// @flow
import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { routes } from '../../constant';
import TextInput from '../TextInput';
import Button from '../Button';
import { validateField } from '../../utils/formValidator';
import './form.scss';

type State = {
  email: string,
  password: string,
  name: string,
  errors: Object,
};

type Props = {
  signUpUser: Function,
};
class SignUpForm extends PureComponent<Props, State> {
  static Body(props: Object) {
    const {
      handleChange, email, password, name, errors,
    } = props;
    return (
      <form className="form__body">
        <TextInput
          placeholder="Name"
          name="name"
          type="text"
          handleChange={handleChange}
          value={name}
          className="form__input"
          error={errors.name}
        />
        <TextInput
          placeholder="Email"
          name="email"
          type="email"
          handleChange={handleChange}
          value={email}
          className="form__input"
          error={errors.email}
        />
        <TextInput
          placeholder="Password"
          name="password"
          type="password"
          handleChange={handleChange}
          value={password}
          className="form__input"
          error={errors.password}
        />
      </form>
    );
  }

  static Footer({ submitForm }: { submitForm: Function }) {
    return (
      <div className="form__footer">
        <Button type="submit" text="signup" handleClick={submitForm} />
        <span className="form__create-account">
          Already have an account?
          <Link to={routes.login}>
            <span> Log in</span>
          </Link>
        </span>
      </div>
    );
  }

  state = {
    name: '',
    email: '',
    password: '',
    errors: {},
  };

  handleChange = (name: string, value: string) => {
    this.setState({
      [name]: value,
    });
    this.validateChange(name, value);
  };

  validateChange = (name: string, value: string) => {
    const { errors } = this.state;
    const { hasError, error } = validateField(name, value);
    if (hasError) {
      errors[name] = error;
    } else {
      delete errors[name];
    }
    this.setState({
      errors: { ...errors },
    });
  };

  submitForm = () => {
    this.props.signUpUser(this.state);
  };

  render() {
    const {
      email, password, name, errors,
    } = this.state;
    return (
      <div className="form">
        <h3 className="form__header">Sign up</h3>
        <SignUpForm.Body
          email={email}
          password={password}
          handleChange={this.handleChange}
          name={name}
          errors={errors}
        />
        <SignUpForm.Footer submitForm={this.submitForm} />
      </div>
    );
  }
}

export default SignUpForm;
