import Validator from 'validatorjs';

const rules = {
  name: 'required|min:3|max:30|alpha',
  email: 'required|email',
  password: 'required|regex:/^(?=.*[a-z])(?=.*[A-Z])(?=.*d)[a-zA-Zd]{8,}$/',
};

export function validateField(name, value) {
  const rule = rules[name];
  const validationResult = { hasError: false };
  if (rule) {
    const validation = new Validator({ [name]: value }, { [name]: rule });
    const hasError = !validation.passes();
    if (hasError) {
      validationResult.hasError = hasError;
      validationResult.error = validation.errors.first(name);
    }
  }
  return validationResult;
}
